export default {
  props: {
    itemInfo: {
      type: Object,
      default: () => {}
    },
    imgList: {
      type: Array,
      default: () => []
    },
    headUrl: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
    }
  },
  mounted() {
 
  },
  methods: {
    handlePreviewImage(url) {
      console.log(url)
      uni.previewImage({
        current: url,
        urls: this.imgList,
        longPressActions: {
          itemList: ['发送给朋友', '保存图片', '收藏'],
          success: function(data) {
            console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片')
          },
          fail: function(err) {
            console.log(err.errMsg)
          }
        }
      })
    },
    
    handleGoDetail(quoteId){
      this.$openPage({ name: 'example',query:{quoteId}})
    }
  }
}
